body {
    border: 3px;
    padding: 3em;
    margin: 0;    
}

.card {
    background: rgb(167, 167, 167);
    background: linear-gradient(38deg, rgb(167, 167, 167) 81%, rgba(0,0,0,0.9953023445706407) 82%);
    margin: 0.5em;
    padding: 1em;
    justify-content: center;
    border-radius: 1em;
    width: 20%;
    height: auto;
    min-width: 170px;
    color: #fff;
    text-align: center;
    box-shadow: 1px 3px 7px 2px rgba(133,129,133,1);
}

.card h2 {
    font-size: 2vw;
}

.card p {
    font-size: 1vw;
}

