.main-heading {
    color: rgba(3,19,36,0.9953023445706407);
    font-size: 7vw;
    font-weight: 700;
    font-family: 'Orbitron', sans-serif;
    margin: 0;
}

hr {
    margin: 2em;
    box-shadow: 1px 1px 1px 1px rgb(102, 102, 102);
}

.scrollbar {
    overflow-y: scroll;
    height: 400px;
}

@media screen and (max-width: 675px) {

    .search-robo {
      width: 70%;
      height: 2rem;
      margin-top: 0.5em;
  }
  
    .card h2 {
    font-size: 3vw;
  }
  
    .card p {
    font-size: 2vw;
  }
  }

@media screen and (max-width: 400px) {
    .scrollbar {
        height: 600px;
    }
}